import axios from 'axios';
import { observer } from 'mobx-react';
import { applySnapshot, getSnapshot } from 'mobx-state-tree';
import React, { FormEvent } from 'react';
import { toast } from 'react-toastify';
import instance from 'src/helpers/instance';
import useAuth from 'src/hooks/useAuth';
import useUsers from 'src/hooks/useUsers';

import Button from '../Layout/Button';
import Form from '../Layout/Form';

type TProps = {
  onClose: () => void;
  openLogin: () => void;
};
const Register: React.FC<TProps> = ({ onClose, openLogin }) => {
  const { setToken } = useAuth();
  const { detailUser: user } = useUsers();
  const {
    username,
    fullname,
    email,
    gender,
    password,
    confirm,
    loading,
    setUsername,
    setFullname,
    setEmail,
    setGender,
    setPassword,
    setConfirm,
    setLoading,
  } = user;

  const handleSubmit = async (e: FormEvent) => {
    try {
      e.preventDefault();
      setLoading(true);
      const response = await instance.post('/user/register', getSnapshot(user));
      setToken(response.data.token);
      onClose();
      toast.success('Táº¡o tÃ i khoáº£n thÃ nh cÃ´ng!');
    } catch (error) {
      if (axios.isAxiosError(error)) {
        toast.error(error.response?.data.error);
      } else {
        toast.error('ÄÃ£ xáº£y ra lá»i, vui lÃ²ng thá»­ láº¡i');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleOpenLogin = () => {
    applySnapshot(user, {});
    onClose();
    openLogin();
  };
  return (
    <Form onSubmit={handleSubmit}>
      <Form.Input label="Username" value={username} onChange={(e) => setUsername(e.target.value)} />
      <Form.Input label="Fullname" value={fullname} onChange={(e) => setFullname(e.target.value)} />
      <Form.Input type="email" label="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
      <Form.Select label="Gender" value={gender} onChange={(e) => setGender(e.target.value)}>
        <option value="male"> Nam </option>
        <option value="famale"> NÆ°Ì </option>
        <option value="other"> KhÃ¡c </option>
      </Form.Select>
      <Form.Input
        type="password"
        label="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        placeholder="******"
      />
      <Form.Input
        type="password"
        label="Confirm"
        value={confirm}
        onChange={(e) => setConfirm(e.target.value)}
        placeholder="******"
      />
      <Button type="submit" variant="block-primary" loading={loading}>
        ÄÄng KyÌ
      </Button>
      <Button type="button" variant="block-outline-second" onClick={() => handleOpenLogin()}>
        ÄÄng NhÃ¢Ì£p
      </Button>
    </Form>
  );
};
export default observer(Register);
